@import "../../style/variables.scss";

.comics {
    &__list {
        display: grid;
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(auto-fill, 225px);
        justify-content: space-between;
        row-gap: 55px;
    }

    &__item {
        transition: 0.3s transform;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;

        &-img {
            width: 225px;
            height: 346px;
            object-fit: cover;
            box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.25);
            margin-bottom: 10px;
        }

        &-title {
            color: #000;
        }

        &-price {
            color: rgba(0, 0, 0, 0.60);
        }

        &:hover {
            transform: translateY(-5px);
        }

        &-enter {
            opacity: 0;
        }

        &-enter-active {
            opacity: 1;
            transition: opacity 500ms ease-in;
        }

        &-exit {
            opacity: 1;
        }
        
        &-exit-active {
            opacity: 0;
            transition: opacity 500ms ease-in;
        }
    }
}